






















import {
  defineComponent, reactive, onMounted, ref,
} from '@vue/composition-api';

import { callbackWhenLastElementShown } from '@/utils/composables/use-callback-when-last-element-shown';
import Paging from '@/models/Paging';
import { MaterialGoods } from '@/models/MaterialGood';
import useLoader from '_/composables/use-loader';
import axios from '../../api/axios';
import Navigator from './components/Navigator.vue';
import MaterialList from './components/CommodityList.vue';
import MaterialItem from './components/CommodityItem.vue';


export default defineComponent({
  components: {
    Navigator,
    MaterialList,
    MaterialItem,
  },
  setup(_, { root }) {
    const { type, colorFamily, id } = root.$route.query;

    const material = ref<MaterialGoods>(null);
    const similarList = ref([] as MaterialGoods[]);


    async function getMaterial() {
      material.value = (await axios({
        method: 'get',
        url: '/mall/material',
        params: {
          id,
        },
      })).data;
    }

    const loading = ref<boolean>(false);
    const params = reactive({
      pageNumber: 1,
      pageSize: 20,
    });

    const disableFetch = ref(false);

    async function getSimilar() {
      if (params.pageNumber === 1) loading.value = true;
      try {
        if (disableFetch.value) return;
        const { records, total } = (await axios.get<Paging<MaterialGoods>>('/mall/mall/resemble/list', {
          params: {
            type,
            colorFamily,
            id,
            ...params,
          },
        })).data;
        params.pageNumber += 1;
        similarList.value.push(...records);
        if (total <= similarList.value.length) {
          disableFetch.value = true;
        }
      } catch (e) {
        root.$errorHandler(e);
      } finally {
        loading.value = false;
      }
    }

    useLoader(getSimilar, params);

    // callbackWhenLastElementShown(similarList, getSimilar, '.material-item');
    callbackWhenLastElementShown(similarList, () => { params.pageNumber += 1; }, '.material-item');

    onMounted(async () => {
      await getMaterial();
    });

    async function preview() {
      const imgSrc = material.value?.imgJson.thumbnail;
      if (imgSrc) {
        await root.$ImageViewer.show(imgSrc, {
          targetElems: '[data-img-type=\'material-item\']',
        });
      }
    }

    return {
      material,
      similarList,
      loading,
      preview,
    };
  },
});
